.properties-wapper {
  height: 40vh;
  position: relative;
  text-align: center;
  margin-top: 2.5rem;
}
.properties-wapper img {
  display: block;
  width: 100%;
  height: 40vh;
  object-fit: cover;
  filter: brightness(30%);
}

.properties-wapper .position {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: var(--light);
  font-size: 2rem;
  line-height: 1.5;
}

.properties-wapper .btn {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  margin-top: 2rem;
}